import zdjecie from './minecraft.jpg';
import Link from '../Link';

export default function GlownyContentOMnie() {
    return (
        <div className="grid grid-cols-2 md:ml-0 md:grid-cols-3">
            <div id="lewo" className="hidden md:block">

            </div>
            <div id="glowneOkno" className="my-2">
                <div className="m-4">
                    <h1 className="text-2xl mt-4">
                        # Moja historia 
                    </h1>
                    <h2 className="mt-14">
                        &nbsp;
                        Dzie Dobry, mam na imi Jakub i na czas pisania yj ju 20 lat i 6 miesicy.
                        Moja mio do uywania komputerw zacza si ju w bardzo modym wieku .
                    </h2>
                    <h2 className="mt-4">
                        
                        Jako mae dziecko miaem kontakt z czym co ciko nazwa komputerem,
                        mianowicie bya to zabawka laptop, ktra nauczya mnie czytania i liczenia
                        jeszcze przed szko. Za to jestem bardzo wdziczny technologii.
                        Wszechobecne informacje pomagaj nam rozwija si w zaskakujcym tempie.
                        Mamy dostp do tego o czym nasi przodkowie mogli co najwyej pomarzy...
                    </h2>
                    <h2 className="mt-4">
                        &nbsp;
                        Przez wszystkie lata szkoy podstawowej i gimnazjum moim hobby byy
                        gry komputerowe. Uwielbiaem uczucie przeniesienia sie do tego
                        sztucznie wykreowanego wiata, zatracaem si w nim godzinami,
                        potrafiem nie koczy gier fabularnych tylko daltego, e baem si
                        i nigdy ju nie powrc do ich wspaniaego wiata 
                        <img src={zdjecie} alt="wiat minecraft wczesna wersja" className="mt-2"/>
                        
                    </h2>
                    <h2 className="mt-2">
                        (Oczywicie Minecraft jest tutaj zym przykadem, jest to sandbox wic
                        przygoda na nim nie ma koca! )
                    </h2>
                    <h2 className="mt-4">
                        Po Gimnazjum wybraem si do 
                        {<Link tekst="Technikum nr4 w Nowym Sczu" link={"https://zsnr4.net/"}/>}.
                        Dopiero w poowie toku nauczania zainteresowaem si programistyk. 
                        Cho nie jestem wybitny w tych klimatach to bardzo urzek mnie jzyk C 
                        i C++, zwizane z nimi zarzdzanie pamici, a take ich potga.
                    </h2>
                    <h2 className="mt-4">
                       
                    </h2>
                </div>
            </div>
            <div id="prawo" className="">

            </div>
        </div>
    );
}