
export default function Link({ link, tekst, ikonka}) {
    return (
        <a href={link}
            className=" border-b-2 border-black border-dotted ml-2
                        hover:bg-black hover:text-white hover:font-bold transition-all duration-200 ease-in-out">
            {tekst}
            {ikonka}
        </a>
    );
}