import {Link} from 'react-router-dom'

import {TbLetterJ} from "react-icons/tb"
import BlokNavBar from "./BlokiNavBar";

export const HomeLink = () => (
    <Link to="/">
        <div className=" border-b-[2px] border-black border-solid"><TbLetterJ size={27}/></div>
    </Link>
  );
  

function NavBar() {
    // to co ma by umieszczone w blokach po prawej stronie navbaru
    const napisyDoBlokow = ["O mnie", "Kontakt"];
    const linkiDoBlokow = ["/about", "/kontakt"];

    return (
        <div id="navbar" className="sticky top-0 flex flex-wrap justify-between items-center border-b-2 
        border-dashed border-gray-200 p-3 bg-white z-10">
            <HomeLink />
            <div className="">
                <BlokNavBar tablica={napisyDoBlokow} tablicaLinkow={linkiDoBlokow}/>
            </div>
        </div>
    );
}

export default NavBar;