import MniejszyHeader from "./MniejszyHeading";
import Link from "../Link";
import Zdjecie from "./Zdjecie";
import zdjAsio from './sdgfh.jpg';
import zdjJd from './jd.jpg'
import Footer from "../komponenty-mainpage/footer";

import { AiFillGithub } from 'react-icons/ai';


function NapisPowitanie(props) {
    return <h1 className=" m-4 text-center text-2xl font-semibold">{props.tekst}</h1>
}

function PoleCPP() {
    return (
        <div className="grid grid-cols-2 md:ml-0 md:grid-cols-3">
            <div id="lewo" className="hidden md:block">\

            </div>
            <div className="m-4">
                <div id="glowneOkno" className="my-2">
                    <NapisPowitanie tekst={"Wisielec LAN przy uyciu ASIO/C++"} />
                    <h1>
                        <Link tekst={`Repozytorium projektu`}
                            link="https://github.com/Destray32/Networking-1"
                            ikonka={<AiFillGithub className="inline-block ml-1 mb-1" size={21} />}
                        />
                    </h1>
                    <MniejszyHeader tekst={"# Wstep"} className={"mt-9"} />
                    <p id="paragrafASIO">
                        Na pocztku chciabym napomnie, e jest to bardzo amatorski projekt,
                        ktry mia za zadanie jedynie sprawdzi proste dziaanie biblioteki ASIO.
                        Rozwizania przedstawione w projekcie s bardzo proste i nie do koca efektowne,
                        jest to stricte podoe pod czyst zabaw .
                        W przyszoci mam zamiar przedstawi "gr" w adnym GUI. Na celowniku miaem
                        przez jakis czas &nbsp;
                        <Link link={"https://github.com/ocornut/imgui"} tekst={"Dear ImGui"} />.
                    </p>
                    <MniejszyHeader tekst={"# ASIO/C++, a co to w ogle jest?"} className={"mt-9"} />
                    <p id="paragrafASIO">
                        Po krtce, ASIO to wieloplatformowa biblioteka napisana w jzyku programowania C++.
                        Uywana jest to <b>programowania sieciowego i niskopoziomowych operacji I/O.</b> Jej
                        twrc jest &nbsp;
                        <Link link={"https://github.com/chriskohlhoff"} tekst="Christopher M. Kohlhoff" />,
                        a repozytorium ASIO moe zosta znalezione na profilu, do ktrego odnosi powyszy link.
                    </p>
                    <Zdjecie zdjecie={zdjAsio} />
                    <p id="paragrafASIO">
                        Bibloteke te mozna sobie wyobraa jako abstrakcje dla czystego networkingu
                        ze stajni CPP. Ukrywa ona przed nami budowe wymiany informacji
                        i pozwala sie skupic na kreatywnym rozwizaniu naszej siatki client-serwer.
                    </p>
                    <p id="paragrafASIO">
                        Dla lepszego i szybszego wytumaczenia dziaania biblioteki ASIO C++ odsyam
                        Ci czytelniku, do kogo mdrzejszego i posiadajcego wiksze dowiadczenie
                        ni ja . Mianowicie &nbsp;
                        <Link link={"https://www.youtube.com/c/javidx9"} tekst="javidx9" />
                        &nbsp; posiada ca seri filmikw, powiconych ASIO i budowaniu komunikacji client/serwer
                        dla gier MMO. Serdecznie zapraszam, aby dowiedzie si wiele ciekawych informacji
                        i nauczy czego nowego.
                    </p>
                    <a href="https://youtu.be/2hNdkYInj4g" >
                        <div className="hover:scale-105 transition-all duration-150 ease-in-out">
                            <Zdjecie zdjecie={zdjJd} />
                        </div>
                    </a>
                    <p id="paragrafASIO">
                        W filmiku poruszane jest wiele wanych spraw takich jak asynchroniczno
                        w odbieraniu danych  itp.
                    </p>
                    <Footer className={"mt-4 mb-16"} />



                </div>
            </div>
            <div id="prawo" className="">

            </div>
        </div>
    );
}

export default PoleCPP;