import { Chrono } from "react-chrono";
import data from "../../data/timeline-data"
import { teksty, ikonki } from "../../data/timeline-tekst";
import { useState } from "react";

import Link from "../Link"

const TimeLine = () => {

    const [display, setDisplay] = useState(1);

    const CallBack = (props) => {
        setDisplay(props.key);
    }

    const DymkiTimeLine = ({ tekst, ikonka, link, nazwaLinku }) => (
        <div className=" text-center font-light animate-[fadein_0.8s]">
            {tekst}<span className=" text-xl">{ikonka}</span>
            <a href={link}>{nazwaLinku}</a>
        </div>
    );

    return (
        <div className="w-[350px] h-max lg:w-[650px]">
            <Chrono disableNavOnKey={true} cardWidth={300} cardHeight={100} hideControls={true}
                enableOutline={false} disableClickOnCircle={false} borderLessCards={true}
                allowDynamicUpdate={true}
                onItemSelected={CallBack}
                items={data} mode="VERTICAL"
                theme={{
                    primary: 'black',
                    secondary: 'white',
                    cardBgColor: 'white',
                    cardForeColor: 'black',
                    titleColor: 'black',
                    titleColorActive: 'white',
                }}>
                <div>
                    {display === 1 && <DymkiTimeLine tekst={teksty[0]} ikonka={ikonki[0]} />}
                </div>
                <div>
                    {display === 2 && <DymkiTimeLine tekst={teksty[1]} ikonka={ikonki[1]} />}
                </div>
                <div>
                    {display === 3 &&
                        <div className="text-center font-light animate-[fadein_0.8s]">
                            Ukoczenie pierwszego "udanego" projektu - gra mobilna &nbsp;
                            <Link link={"https://play.google.com/store/apps/details?id=com.Destray.SpaceFighter"}
                                tekst={"Space Fighter "}
                            />
                        </div>}
                </div>
                <div>
                    {display === 4 && <DymkiTimeLine tekst={teksty[3]} ikonka={ikonki[3]} />}
                </div>
                {/* <div>
                    {display === 5 && <DymkiTimeLine tekst={teksty[4]} ikonka={ikonki[4]} />}
                </div>
                <div>
                    {display === 6 && <DymkiTimeLine tekst={teksty[4]} ikonka={ikonki[4]} />}
                </div> */}

            </Chrono>
        </div>
    );
}

export default TimeLine;