import {Link} from 'react-router-dom'


export default function Notatka({tekst, tekst2, czas, link}){
    return(
        <Link to={link}>
            <article className=" bg-gray-50 h-max text-left p-6 rounded-lg m-6
            border-2 border-gray-200 border-solid hover:bg-gray-200 hover:scale-105 transition-all duration-150 ease-in-out">
                <h1 className="text-xl">{tekst}</h1>
                <p className=" text-left text-gray-500 text-sm">{tekst2}</p>
                <p className="relative top-[15px] text-xs text-gray-400 font-semibold">{czas}</p>
            </article>
        </Link>
    );
}