import React from 'react';
import ReactDOM from 'react-dom/client';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter, Route, Routes } from 'react-router-dom';

import './index.css';
import App from './App';
import AppDwa from './routes/AppDwa';
import AppASIO from './routes/AppASIO';
import AppKontakt from './routes/AppKontakt';
import AppOMnie from './routes/AppOMnie';



const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <Routes>
        <Route path='/' element={<App />} />
        <Route path='/test' element={<AppDwa />}/>
        <Route path='/cppasio' element={<AppASIO />}/>
        <Route path='/kontakt' element={<AppKontakt />}/>
        <Route path='/about' element={<AppOMnie />}/>
      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
