import { Link } from 'react-router-dom';


function BlokNavBar({ tablica, tablicaLinkow }) {

    const bloki = tablica.map((napis, key) =>
        <a id={key} className=" text-center m-1 p-2 cursor-pointer hover:bg-slate-100 hover:rounded-lg 
        transform hover:scale-125 transition-all">
            <Link to={tablicaLinkow[key]}>
                {napis}
            </Link>
        </a>
    );

    return (
        <div>{bloki}</div>
    );
}

export default BlokNavBar;