import zdjecie from "./zdjecie.jpg"
import TimeLine from "./Home";
import Notatki from "./Notatki"
import Footer from "./footer";

function NapisPowitanie(props) {
    return <h1 className=" m-4 text-center text-2xl font-semibold">{props.tekst}</h1>
}


function Pole() {
    return (
        <div className="grid grid-cols-2 md:ml-0 md:grid-cols-3 ">
            <div id="lewo" className="hidden md:block"></div>

            <div id="glowneOkno" className="my-2">
                <div className="relative flex items-center left-14">
                    <img className=" w-12 h-12 rounded-full my-7" src={zdjecie}></img>
                    <NapisPowitanie tekst={"Witaj na mojej stronie internetowej! "} />
                </div>
                <p className="relative left-14 text-center">Nazywam sie Jakub Bednarek.
                    Jestem programista i grafikiem komputerowym 
                </p>
                <TimeLine />
                <div className="m-4">
                    <Notatki />
                    <Footer className={"mb-14 mt-4"} />
                </div>
            </div>

            <div id="prawo" className=""></div>
        </div>
    );
}

export default Pole;